body{
	background-color: #10273C;
}
.App{
	background-image: linear-gradient(45deg,#2E86C1, #fff);
	min-height: 500px;
	width: 500px;
	margin: 40px auto;
	border-radius: 10px;
}
#form input{
	background-color: #154360;
	border: 0;
	width: 300px;
	height: 50px;
	padding: 0 20px;
	margin: 20px;
	border-radius: 10px;
	color: #fff;
}
#form input:focus{
	outline: none;
}
#form button{
	height: 50px;
	width: 80px;
	border: 0;
	border-radius: 10px;
	font-weight: bolder;
	cursor: pointer;
}